export default {
	name: 'demo',
	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			title: 'Hello', //头部
			scrollTop: 0, //距离顶部
			old: { //旧的顶部距离
				scrollTop: 0
			},
			indicatorDots: true, //显示指示点
			autoplay: true, //自动播放
			interval: 2000, //动画时长
			duration: 500, //动画间隔
			movable: { //客服初始位置
				x: 0,
				y: 200,
			},
			texts: [
				'HBuilder，200万开发者选择的IDE',
				'MUI，轻巧、漂亮的前端开源框架',
				'wap2app，M站快速转换原生体验的App',
				'5+Runtime，为HTML5插上原生的翅膀',
				'HBuilderX，轻巧、极速，极客编辑器',
				'uni-app，终极跨平台方案',
				'HBuilder，200万开发者选择的IDE',
				'MUI，轻巧、漂亮的前端开源框架',
				'wap2app，M站快速转换原生体验的App',
				'5+Runtime，为HTML5插上原生的翅膀',
				'HBuilderX，轻巧、极速，极客编辑器',
				'uni-app，终极跨平台方案'
			],
			text: '',
			canAdd: true,
			canRemove: false,
			extraLine: [],
			//复选框
			items: [{
					value: 'USA',
					name: '美国'
				},
				{
					value: 'CHN',
					name: '中国',
					checked: 'true'
				},
				{
					value: 'BRA',
					name: '巴西'
				},
				{
					value: 'JPN',
					name: '日本'
				},
				{
					value: 'ENG',
					name: '英国'
				},
				{
					value: 'FRA',
					name: '法国'
				}
			],
			checkArr: [], //选中的数据

			editorCtx: null, //接收富文本内容
			date: currentDate,
		}
	},
	onLoad() {

	},
	methods: {
		navto() {
			this.$router.push({
				path: '/pages/list/list',
				query: {
					id: 1
				}
			});
			uni.$emit('update', {
				msg: 'ddd'
			});
			uni.$on('update', function(data) {
				console.log(data)
			})
		},
		//滚动到顶部
		upper(e) {
			// console.log(e)
		},
		//滚动到底部
		lower(e) {
			// console.log(e)
		},
		//滚动事件
		scroll(e) {
			// console.log(e);
			this.old.scrollTop = e.detail.scrollTop;
		},
		//点击回到顶部
		goTop(e) {
			console.log(e)
			this.scrollTop = this.old.scrollTop;
			this.$nextTick(function() {
				this.scrollTop = 0
			})
			uni.showToast({
				icon: "none",
				title: "纵向滚动 scrollTop 值已被修改为 0"
			})
		},
		//获取手机号
		getMobile(e) {
			console.log(e)
		},
		//是否展示指示点
		changeIndicatorDots(e) {
			this.indicatorDots = e.detail.value;
		},
		//是否自动播放
		changeAutoplay(e) {
			this.autoplay = e.detail.value;
		},
		//幻灯片切换时长
		durationChange(e) {
			console.log(e)
			this.duration = e.detail.value;
		},
		//自动播放时长
		intervalChange(e) {
			console.log(e);
			this.interval = e.detail.value;
		},
		//客服按钮距离改变
		movableChange(e) {
			let width = window.innerWidth / 2;
			this.movable.x = 0;
			if (e.detail.x < width) {
				this.movable.x = 0;
				this.movable.y = e.detail.y;
			} else {
				this.movable.x = width * 2
				this.movable.y = e.detail.y;
			}
		},
		//添加内容
		add() {
			this.extraLine.push(this.texts[this.extraLine.length % 12]);
			this.text = this.extraLine.join('\n');
			this.canAdd = this.extraLine.length < 12;
			this.canRemove = this.extraLine.length > 0;
		},
		//删除内容
		remove() {
			if (this.extraLine.length > 0) {
				this.extraLine.pop();
				this.text = this.extraLine.join('\n');
				this.canAdd = this.extraLine.length < 12;
				this.canRemove = this.extraLine.length > 0;
			}
		},
		//复选框数据
		checkboxChange(e) {
			this.checkArr = e.detail.value;
		},
		//初始化加富文本内容赋值给变量
		hasInput(e) {
			this.editorCtx = e.detail.html;
		},
		//撤销
		bindDateChange(e) {
			this.date = e.detail.value;
		},
		//日期格式转换
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		}
	}
}
